/*
* Provides rendering of a table which is using the given FlexibleTableHeader set in
* order to tabulate the given data. As per definition of earch header component,
* a column could be hidden, sortable, or draggable. Each table row can expand/collapse
* or respond to a click action.
*/
import {
    Component,
	Input,
	Output,
	EventEmitter
} from '@angular/core';

import { Http } from '@angular/http';

@Component({
	selector: 'flexible-auto-complete',
	template: `
<label for="">
    <span class="off-screen" [textContent]="message"></span>
    <input [value]="entry"
        (keyup)="keyup($event)" />
    <span [class]="icon" *ngIf="filteredData.length == 0" [class.icon]="true" area-hidden="true"></span>
    <span class="icon fa fa-reply" *ngIf="icon && filteredData.length" area-hidden="true"></span>
</label>
<ul *ngIf="filteredData.length" 
    role="list" 
    class="viewport" 
    [style.max-width]="direction === 'horizontal' ? viewport : null"
    [style.overflow-x]="direction === 'horizontal' ? 'auto' : null"
    [style.max-height]="direction === 'vertical' ? viewport : null"
    [style.overflow-y]="direction === 'vertical' ? 'auto' : null">
    <li role="listitem" tabindex="0" *ngFor="let item of filteredData" (keyup)="clickup($event, item)" (click)="selectTab(item)">
        <ng-container  
            [ngTemplateOutlet]="template ? template : defaultTemplate" 
            [ngTemplateOutletContext]="{data: item}"></ng-container>
        <span class="icon fa fa-reply" area-hidden="true"></span>
    </li>
</ul>

<ng-template #defaultTemplate let-detail="data">
  <span *ngFor="let x of keymap" [textContent]="detail[x] + ' '"></span>
</ng-template>
`,
	styles: [`:host{display:table;width:100%}:host label{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;border:1px solid #ddd;padding:2px;height:22px;vertical-align:top;border-radius:2px;-webkit-box-shadow:0 2px 2px 0 rgba(0,0,0,.16),0 0 0 1px rgba(0,0,0,.08);box-shadow:0 2px 2px 0 rgba(0,0,0,.16),0 0 0 1px rgba(0,0,0,.08);-webkit-transition:-webkit-box-shadow .2s cubic-bezier(.4,0,.2,1);transition:-webkit-box-shadow .2s cubic-bezier(.4,0,.2,1);transition:box-shadow .2s cubic-bezier(.4,0,.2,1);transition:box-shadow .2s cubic-bezier(.4,0,.2,1),-webkit-box-shadow .2s cubic-bezier(.4,0,.2,1)}:host label input{-webkit-box-flex:1;-ms-flex:1;flex:1;border:0;padding-left:16px}:host label .icon{-webkit-box-flex:0;-ms-flex:0 0 15px;flex:0 0 15px;margin-right:5px;margin-top:3px;color:#ddd}:host .off-screen{display:block;float:left;height:0;overflow:hidden;text-indent:-99999px;width:0}:host .viewport{-webkit-box-sizing:border-box;box-sizing:border-box;list-style-type:none;margin:0;padding:0;z-index:2;border:1px solid #ddd;border-radius:2px;background-color:#fff;overflow:hidden}:host .viewport li{-webkit-box-sizing:border-box;box-sizing:border-box;white-space:nowrap;background-color:#fdfeff;padding:2px 5px}:host .viewport li:hover{background-color:#ff3e58}:host .viewport li:focus{background-color:#3e8bff}:host .viewport li .icon{color:#fff;display:block}`]
})
export class FlexibleAutoCompleteComponent {

	private interval: any;

	entry = "";
	filteredData: any[] = [];

	@Input('keymap')
    public keymap: any[] = [];

    @Input("icon")
    public icon: string;

    @Input("message")
    public message: string;

	@Input("direction")
    public direction = "vertical";

    @Input("delayby")
    public delayby = 300;

    @Input("triggeron")
    public triggeron = 2;

    @Input("source")
    public source: string;

    @Input("viewport")
    public viewport: string;

	@Input("template")
    public template: any;

    @Input("data")
    public data: any;

	@Output("onselect")
	onselect = new EventEmitter();

    constructor(private http: Http) {}

	clickup(event, item) {
		const code = event.which;
		
		if (code === 13) {
			this.selectTab( item );
		}	
	}
	keyup(event) {
        const code = event.which;
		
		if (code === 13) {
			if (this.filteredData && this.filteredData.length) {
				this.selectTab( this.filteredData[0] );
			}
		} else {
			this.entry = event.target.value;
			if (this.interval) {
				clearTimeout(this.interval);
			}
			this.interval = setTimeout( () => {
				const key = this.entry.toLowerCase();
				if (key.length > this.triggeron) {
					if (this.source) {
						this.http.get(this.source + key).subscribe(
							(result: any) => {
								this.data = result.body;
								this.filteredData = result.body;
							}
						);
					} else if (this.data) {
						this.filteredData = this.data.filter( (item) => {
							let keep = false;
							for(let j = 0; j < this.keymap.length; j++) {
								const k = this.keymap[j];
								let tmp = item[k]
								const v = tmp ? tmp.toLowerCase() : undefined;
								if (v && v.indexOf(key) >= 0) {
									keep = true;
									break;
								}
							}
							return keep;
						});
					}
				} else {
					this.filteredData = [];
				}
			}, this.delayby);
		}
	}
	selectTab(item) {
		this.onselect.emit(item);
		this.filteredData = [];
		this.entry = "";
	}
}
